html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font: 1.6rem "Cairo", sans-serif;
  margin: 0;
}
main {
  margin-top: 5rem;
}
.gg-search {
  box-sizing: border-box;
  position: relative;
  display: block;
  cursor: pointer;
  transform: scale(var(--ggs, 1.1));
  width: 16px;
  height: 16px;
  border: 2px solid;
  border-radius: 100%;
  margin: 0.5rem 1rem 1rem 0.7rem;
}

.gg-search::after {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
  border-radius: 3px;
  width: 3px;
  height: 10px;
  cursor: pointer;
  background: currentColor;
  transform: rotate(-45deg);
  top: 10px;
  left: 12px;
}
.navigation-header {
  width: 100%;
  height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: skyblue;
  box-shadow: 0 2px 6px rgba(219, 219, 219, 0.26);
  padding: 0 1rem;
  z-index: 5;
}
.hamburger-btn {
  display: block;
}

.sidebar ul {
  list-style: none;

}
.sidebar a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.sidebar a.active {
  color: red;
}

.navigation-header-nav-links ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.navigation-header-nav-links li {
  margin: 2rem;
}

.homepage {
  margin: 0 auto;
}

.homepage-search-bar {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 8rem;
  width: 40%;
}

.homepage-search-input {
  width: 100%;
}

.homepage-search--icon {
  background-color: skyblue;
}

.nav-links a {
  display: none;
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a.active {
  color: red;
}

.location-box {
  margin: 1rem auto;
  width: 90%;
  border: 1px solid black;
  text-align: center;
}

.location-box-header {
  display: flex;
  justify-content: space-between;
}

.location-box-favorite {
  display: flex;
  align-items: center;
}
/* 
.location-box-content {
  margin: 0 auto;
} */

.location-box-days-ul {
  padding: 0;
  margin-top: 5rem;
  align-items: center;
  list-style-type: none;
  display: flex;
  flex-direction: column;
}

.location-box-days-ul li {
  border: 1px solid rgb(0, 0, 0);
  margin: 1rem;
  width: 75%;
  height: 20rem;
}

.sidebar {
  position: fixed;
  margin-top: 3rem;
  margin-left: -1rem;
  transition: all 0.5s;
  transform: translateX(-30rem);
  width: 30rem;
  background-color: skyblue;
}
.sidebar.open {
  transform: translateX(0);
}

.sidebar-close-button {
  border-radius: 50%;
  border: 0.1rem #000000 solid;
  width: 3rem;
  height: 3rem;
  padding: 0.5rem;
  font-size: 2rem;
  padding-top: 0;
  cursor: pointer;
  position: absolute;
  right: 0.5rem;
  top: 1.8rem;
}

.favorites-list {
  padding: 0;
  list-style: none;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.favorites-list li {
  cursor: pointer;
  border: 2px solid red;
  margin: 1rem;
  width: 75%;
}

/* web */

@media (min-width: 768px) {
  .nav-links a {
    display: block;
  }

  .hamburger-btn {
    display: none;
  }
  .location-box-days-ul {
    justify-content: space-evenly;
    flex-direction: row;
  }

  .favorites-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
  }
  .favorites-list li {
    width: 14%;
  }
}

.AutoComlete {
  width: 39.9%;
  text-align: left;
  font-size: 20px;
  color:  rgba(0, 0, 0, 0.73);
  list-style: none;
  border: 0.1px solid black;
  margin: auto;
  padding: 0;
  line-height: 1.6;
  
}